.item1 {
  float: none !important;
}

.grid {
  display: flex !important;
  flex-flow: row wrap;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 0 2.5px;
  overflow: hidden;
}
.grid .grid-item {
  width: calc((100% - 2 * 2.5px * 6 ) / 6);
  margin-bottom: 25px;
}
@media screen and (max-width: 1640px) {
  .grid .grid-item {
    width: calc((100% - 2 * 2.5px * 5 ) / 5);
  }
}
@media screen and (max-width: 1440px) {
  .grid .grid-item {
    width: calc((100% - 2 * 2.5px * 4 ) / 4);
  }
}
@media screen and (max-width: 980px) {
  .grid .grid-item {
    width: calc((100% - 2 * 2.5px * 3 ) / 3);
  }
}
@media screen and (max-width: 640px) {
  .grid .grid-item {
    width: calc((100% - 2 * 2.5px * 2 ) / 2);
  }
}
.grid-5 .grid-item {
  width: calc((100% - 2 * 20px * 5 ) / 5);
}
.grid-3 .grid-item {
  width: calc((100% - 2 * 20px * 3 ) / 3);
}
.grid-2 {
  margin: 0 -10px;
}
.grid-2 .grid-item {
  width: calc((100% - 2 * 10px * 2 ) / 2);
  margin-right: 10px;
  margin-left: 10px;
}

.grid-item {
  position: relative;
  margin-right: 2.5px;
  margin-left: 2.5px;
  margin-bottom: 3.5rem;
  overflow: hidden;
}

@media screen and (max-width: 1024px) {
  .grid-5 .grid-item {
    width: calc((100% - 2 * 20px * 3 ) / 3);
  }
}
@media screen and (max-width: 768px) {
  .grid-5 .grid-item, .grid-3 .grid-item {
    width: calc((100% - 2 * 20px * 2 ) / 2);
  }

  .grid-2 .grid-item {
    width: calc((100% - 2 * 10px * 1 ) / 1);
  }
}
@media screen and (max-width: 480px) {
  .grid-5 .grid-item, .grid-3 .grid-item {
    width: calc((100% - 2 * 20px * 1 ) / 1);
  }
}
