.align-right {
  margin-left: auto;
}

.align-left {
  margin-right: auto;
}

.scroll-lock {
  overflow: hidden;
}

.mdl-popup {
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  overflow-y: auto;
  background-color: rgba(0, 0, 0, 0.25);
  z-index: 201;
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
.mdl-popup.visible {
  visibility: visible;
  opacity: 1;
}

.popup-container {
  position: relative;
  width: calc(100% - 2rem);
  max-width: 480px;
  margin: auto;
  padding-top: 2rem;
  padding-right: 3rem;
  padding-bottom: 1rem;
  padding-left: 3rem;
  background-color: white;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  outline: 0;
}
.popup-container .btn-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
}
.popup-container .btn-close::before, .popup-container .btn-close::after {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 2px;
  height: 20px;
  background-color: #333;
  transform: rotate(45deg) translate(-50%, -50%);
  transform-origin: top left;
  content: "";
}
.popup-container .btn-close::after {
  transform: rotate(-45deg) translate(-50%, -50%);
}

.popup-content {
  max-height: 0;
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
  transition: max-height 0.3s ease-in-out, opacity 0.75s ease-in-out;
}
.popup-content.visible {
  visibility: visible;
  opacity: 1;
  max-height: 100vh;
}
.popup-content .btn {
  border: 1px solid transparent;
  border-radius: 3px;
  display: inline-block;
  font-style: normal;
  font-weight: 400;
  padding: 10px 18px;
  cursor: pointer;
  text-align: center;
  transition: border-color 0.1s, box-shadow 0.1s;
  text-decoration: none;
  line-height: 1.5;
}
.popup-content .btn-block {
  width: 100%;
}
.popup-content .btn-primary {
  background-color: #47bb49;
  color: #fff;
}
.popup-content .btn-link {
  line-height: 1.42857143;
}
.popup-content .btn-facebook {
  background-color: #4b70b0;
  box-shadow: inset 0 1px 0 rgba(252, 252, 252, 0.2), 0 1px 1px rgba(0, 0, 0, 0.1);
  border-color: #3e4d6d #2a3959 #2a3959;
  color: #fff;
  background-image: linear-gradient(to bottom left, #4b70b0, #3c5b9a);
}

.popup-header {
  border-bottom: 1px solid #e5e5e5;
}
.popup-header .form-icon {
  display: block;
  margin: 20px auto 10px auto;
}

.popup-title {
  text-align: left;
  line-height: 1.42857143;
}

.frm-group {
  display: flex;
  margin-top: 2rem;
  margin-bottom: 0 !important;
  flex-flow: column nowrap;
}

.input-title {
  font-size: 0.875rem;
  color: #969696;
}

.address {
  margin-top: 2rem;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}
.address .frm-group {
  width: 47%;
  margin-top: 0;
}

.popup-body {
  padding: 1rem 0;
}

.popup-footer {
  text-align: center;
}

.popup-divider {
  position: relative;
  outline: 0;
  border: 0;
  text-align: center;
  opacity: 0.5;
  margin: 12px 0;
  height: 15px;
}
.popup-divider::before {
  content: "";
  background: linear-gradient(90deg, transparent, #818078, transparent);
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 1px;
}
.popup-divider::after {
  content: attr(data-content);
  position: relative;
  display: inline-block;
  padding: 0 12px;
  background: #fff;
}

.frm-control {
  display: block;
  width: 100%;
  height: 34px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #555;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.08);
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}

.frm-control:focus {
  border-color: #66afe9;
  outline: 0;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.08), 0 0 8px rgba(102, 175, 233, 0.6);
}

.emptycart-warning {
  padding: 15px;
  border: 1px solid #ccc;
  margin: auto;
}

.freeshippingtemptation {
  padding: 18px 12px;
  margin-bottom: 10px;
  background-color: #ebf0f4;
  color: #2f5fb4;
}

.freeshippingtemptationtext {
  font-size: 14px;
  vertical-align: top;
}

.freeshippingtemptation .fa-info-circle {
  font-size: 20px;
}

.checkout {
  background-color: #f5f5f5;
  width: auto;
  height: auto;
}

.cart-content {
  padding-top: 12px;
  display: flex;
  flex-direction: row;
}

.cart-panel {
  flex-grow: 1;
  height: 100%;
  margin-right: 40px;
  border-radius: 3px;
  background: #fff;
  border: 1px solid #e5e5e5;
  padding: 12px;
}

.cart-panel-title {
  border-bottom: 1px solid #e5e5e5;
  padding-bottom: 10px;
}

.cart-panel-title h1 {
  color: #000;
  font-size: 2em;
  font-weight: 400;
}

.cart-items {
  margin-top: 10px;
  display: block;
  /*grid-template-columns: 120px 1fr 104px 86px 55px;
  grid-row-gap: 10px;*/
}

.cart-item {
  /*display: contents;*/
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 120px 1fr 104px 86px 55px;
  grid-template-columns: 120px 1fr 104px 86px 55px;
  padding: 10px 0;
}
.cart-item > * {
  -ms-grid-row-align: center;
}
.cart-item-img {
  align-self: center;
  -ms-grid-column: 1;
}
.cart-item-description {
  align-self: center;
  -ms-grid-column: 2;
}
.cart-item-quantity {
  align-self: center;
  -ms-grid-column: 3;
}
.cart-item-quantityMinus a {
  color: #9a9fad;
}
.cart-item-quantityValue {
  padding: 0px 3px 0px 3px;
}
.cart-item-quantityValue input:disabled {
  color: #9a9fad;
  text-align: center;
  width: 50px;
  padding-left: 15px;
}
.cart-item-quantityPlus a {
  color: #9a9fad;
}
.cart-item-pricing {
  align-self: center;
  text-align: end;
  -ms-grid-column: 4;
}
.cart-item-compareAtPrice {
  text-decoration: line-through;
  color: #9a9fad;
  font-size: 14px;
}
.cart-item-price {
  font-size: 14px;
}
.cart-item-discount {
  font-size: 12px;
  margin-top: 5px;
}
.cart-item-volumeDiscount {
  color: #5fc957;
}
.cart-item-regularDiscount {
  color: #eb595e;
}
.cart-item-remove {
  align-self: center;
  text-align: end;
  padding: 0 12px;
  -ms-grid-column: 5;
}
.cart-item-remove i {
  font-size: 15px;
}
.cart-item-remove a {
  color: #9a9fad;
}
.cart-item-remove i:hover {
  color: #e12727;
}

.cart-sidebar {
  height: 100%;
  width: 400px;
  border-radius: 3px;
  background: #fff;
  border: 1px solid #e5e5e5;
  padding: 24px;
}
.cart-sidebar-title {
  border-bottom: 1px solid #e5e5e5;
  padding-bottom: 10px;
  color: #000;
  font-size: 2em;
  font-weight: 400;
}
.cart-sidebar-cart-items {
  margin-top: 18px;
  display: grid;
  grid-template-columns: 65px 1fr 120px;
  grid-template-rows: auto;
  row-gap: 20px;
}
.cart-sidebar-cart-item-img {
  grid-column: 1;
  align-self: center;
}
.cart-sidebar-cart-item-img img {
  width: 60px;
}
.cart-sidebar-cart-item-description {
  grid-column: 2;
  align-self: center;
}
.cart-sidebar-cart-item-pricing {
  grid-column: 3;
  align-self: center;
  text-align: end;
}
.cart-sidebar-cart-item-quantity {
  color: #9a9fad;
}
.cart-sidebar-cart-item-compareAtPrice {
  text-decoration: line-through;
  color: #9a9fad;
  font-size: 14px;
}
.cart-sidebar-cart-item-price {
  font-size: 14px;
}
.cart-sidebar-total-items {
  margin-top: 18px;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 180px 1fr;
  grid-template-columns: 180px 1fr;
  -ms-grid-rows: 1fr 1fr 1fr 1fr;
  grid-template-rows: auto;
  row-gap: 20px;
}
.cart-sidebar-total-item-text {
  -ms-grid-column: 1;
  display: block;
}
.cart-sidebar-total-item-text:nth-child(1) {
  -ms-grid-row: 1;
}
.cart-sidebar-total-item-text:nth-child(3) {
  -ms-grid-row: 2;
}
.cart-sidebar-total-item-text:nth-child(5) {
  -ms-grid-row: 3;
}
.cart-sidebar-total-item-value {
  -ms-grid-column: 2;
  font-size: 14px;
  text-align: right;
}
.cart-sidebar-total-item-value:nth-child(2) {
  -ms-grid-row: 1;
}
.cart-sidebar-total-item-value:nth-child(4) {
  -ms-grid-row: 2;
}
.cart-sidebar-total-item-value:nth-child(6) {
  -ms-grid-row: 3;
}
.cart-sidebar-total-text {
  font-weight: 700;
}
.cart-sidebar-total-value {
  font-size: 14px;
  font-weight: 700;
}
.cart-sidebar-checkout {
  -ms-grid-column: 1;
  -ms-grid-column-span: 3;
  -ms-grid-row: 4;
  grid-column: 1/3;
  text-align: center;
}
.cart-sidebar-checkout-btn {
  background: #85c96f;
  width: 100%;
  align-self: center;
  color: #fff;
  display: inline-block;
  padding: 12px;
  border: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 13px;
  font-weight: 400;
  line-height: 1;
  text-transform: uppercase;
  color: #fff;
}
.cart-sidebar-checkout-btn:hover {
  background: #95d97f;
  cursor: pointer;
}
.cart-sidebar-item-discount {
  color: #eb595e;
}

.cart-divider-cart-item {
  grid-column-start: 1;
  grid-column-end: 6;
  border-top: 1px solid #e5e5e5;
}

.cart-divider {
  border-top: 1px solid #e5e5e5;
}

@media only screen and (max-width: 990px) {
  .cart-panel {
    margin-right: 0px;
  }

  .cart-sidebar {
    margin-top: 30px;
    width: 100%;
  }

  .cart-content {
    flex-direction: column;
  }

  .cart-items {
    margin-top: 10px;
    display: grid;
    grid-template-columns: auto;
    grid-row-gap: 10px;
  }

  .cart-item {
    -ms-grid-columns: 92px 1fr 100px 50px;
    grid-template-columns: 92px 1fr 100px 50px;
    /*120px 1fr 100px 50px;*/
    -ms-grid-rows: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }

  .cart-item-img {
    -ms-grid-row: 1;
    -ms-grid-row-span: 2;
    align-self: center;
    grid-row: 1/span 2;
  }

  .cart-item-description {
    align-self: center;
    -ms-grid-column: 2;
    -ms-grid-column-span: 3;
    grid-column-start: 2;
    grid-column-end: 5;
  }

  .cart-item-quantity {
    align-self: center;
    -ms-grid-row: 2;
    -ms-grid-column: 2;
    grid-row: 2;
  }

  .cart-item-pricing {
    align-self: center;
    text-align: end;
    -ms-grid-row: 2;
    -ms-grid-column: 3;
    grid-row: 2;
  }

  .cart-item-remove {
    align-self: center;
    text-align: end;
    padding: 0 12px;
    -ms-grid-row: 2;
    -ms-grid-column: 4;
    grid-row: 2;
  }

  .cart-divider-cart-item {
    grid-column-end: 2;
  }
}
@media screen and (max-width: 414px) {
  .cart-item {
    -ms-grid-columns: 1fr 100px 1fr;
    grid-template-columns: 1fr 100px 1fr;
    -ms-grid-rows: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }

  .cart-item-description {
    grid-column-end: 4;
  }

  .cart-item-quantity,
.cart-item-pricing,
.cart-item-remove {
    -ms-grid-row: 3;
    grid-row: 3;
  }

  .cart-item-quantity {
    -ms-grid-column: 1;
  }

  .cart-item-pricing {
    -ms-grid-column: 2;
  }

  .cart-item-remove {
    -ms-grid-column: 3;
  }
}
html, body {
  margin: 0;
}

@media only screen and (max-width: 767px) {
  .navibar-header {
    z-index: 200;
  }
}
@media only screen and (max-width: 980px) {
  .product-carasoul {
    margin-left: 0;
  }

  .nav-filter {
    grid-template-columns: 1fr 1fr;
  }
}